import './card.css';

export default function Card({title, imageSrc, description, link}) {
  return (
    <div>
        <div className="card">
            <div className="imageContainer ">
              <img style={{width: "100%"}} className="img img-fluid" src={imageSrc} alt="" />{/*image size height x width 2112 x 1920*/}
            </div>
            <div className="card-body">
                <h5 className="card-title">{title}</h5>
                <p className="card-text">{description}</p>
                <a rel="noreferrer" target={'_blank'} href={link} className="btn btn-primary">Visitez le website</a>
            </div>
        </div>
    </div>
  )
}
